"use client";

export default function Hero() {
  return (
    <section className="w-full bg-gray-100 rounded-2xl p-10 flex flex-col md:flex-row items-center justify-between">
      <div className="max-w-xl space-y-4">
        <h1 className="text-4xl md:text-5xl font-bold">
          Shop the Latest Products at the Best Prices
        </h1>

        <p className="text-gray-600">
          Discover exclusive deals on electronics, fashion, home appliances and more.
        </p>

        <button className="bg-black text-white px-6 py-3 rounded-xl">
          Shop Now
        </button>
      </div>

      <img
        src="/hero.png"
        className="w-80 md:w-96 mt-8 md:mt-0"
        alt="Shopping banner"
      />
    </section>
  );
}
